// ุฅุตูุงุญ ูุดููุฉ toLowerCase
const safeToLowerCase = (value: any): string => {
  if (typeof value === 'string') return value.toLowerCase();
  if (value && typeof value.toString === 'function') return value.toString().toLowerCase();
  return 'unknown';
};

// ----------------------------------------
// TYPES & INTERFACES
// ----------------------------------------

type Libraries = ("places" | "geometry" | "visualization")[];
const libraries: Libraries = ["places", "geometry", "visualization"];

interface Location {
  id: string;
  name: string;
  lat: number;
  lng: number;
  population?: number;
  type: string;
  radius: number;
}

interface Keyword {
  id: string;
  text: string;
  searchVolume: number;
  competition: string;
  cpc: number;
  type: string;
  matchType: string;
}

interface TimeSlot {
  id: string;
  startTime: string;
  endTime: string;
  days: string[];
}

interface AudienceItem {
  id: string;
  label: string;
  percentage: number;
  selected: boolean;
}

interface AudienceData {
  ageGroups: AudienceItem[];
  genders: AudienceItem[];
  interests: AudienceItem[];
}

interface AdAsset {
  id: string;
  type: 'headline' | 'description' | 'image' | 'video' | 'phone';
  content: string;
  file?: File;
  preview?: string;
  performance?: {
    ctr: number;
    impressions: number;
    clicks: number;
  };
}

interface CampaignGoal {
  id: string;
  name: string;
  description: string;
  icon: React.ReactNode;
  color: string;
  metrics: string[];
}

interface CampaignType {
  id: string;
  name: string;
  description: string;
  icon: React.ReactNode;
  color: string;
  features: string[];
  bestFor: string[];
}

interface BiddingStrategy {
  id: string;
  name: string;
  description: string;
  icon: React.ReactNode;
  color: string;
  pros: string[];
  cons: string[];
}

interface DevicePerformance {
  desktop: { impressions: number; clicks: number; ctr: number; cost: number };
  mobile: { impressions: number; clicks: number; ctr: number; cost: number };
  tablet: { impressions: number; clicks: number; ctr: number; cost: number };
}

interface PerformancePrediction {
  scenario: string;
  impressions: number;
  clicks: number;
  conversions: number;
  cost: number;
  roas: number;
  confidence: number;
}

